import React, { PureComponent } from "react";
import { v4 as uuidv4 } from 'uuid';
import Iframe from 'react-iframe';
import ReactPlayer from "react-player"
import PopUpTikTok from "../../../Components/PostPopUp/Elements/PopUpTikTok";
import { POPUP_VIDEO_RENEW } from '../../../../actions/themeActions'
import { GET_MEDIA_NAME } from "../../../../utils";
const videoIframeTyle = ["youtube", "youtu.be", "vimeo", "soundcloud"]

const isVideo = (videoLink) => {
    let isVideoStatus = false;
    if (String(videoLink)) {
        videoIframeTyle.map((item) => {
            if (String(videoLink).includes(item) && !isVideoStatus) isVideoStatus = true;
        })
    }
    return isVideoStatus;

}
export default class PopUpVideo extends PureComponent {
    constructor(props) {
        super(props);
    }
    componentDidMount() {
        const { data } = this.props;
        let updateVideo = document.querySelector(`#video_p_${data.id}`);
        if (updateVideo) {
            updateVideo.play()
            updateVideo.muted = true;
        }
    }

    render() {
        const { data, wall } = this.props;

        return <div className="tb_hs_video_wrap" key={data.id}>
            {
                isVideo(data.link) ? <Iframe
                    className="tb_hs_media_iframe_video" key={`v_v_${data.id}`}
                    frameBorder="0"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    src={`${data.mediaUrl}?autoplay=1&loop=1&muted=1&controls=0&volume=0`}
                    allowFullScreen="true"
                    data-filter-id={data.filterId} data-type="video"
                    data-network={data.network.id}
                    data-link={data.link}
                    data-wall-id={wall.Wall.id} data-item-id={data.id}
                    data-load={0}
                    height="1000"
                    width="1000"
                    autoplay={true}
                    muted={true}
                    loop={true}
                    playsinline=""
                ></Iframe> : ["tumblr", "facebook", "linkedin"].includes(data.link) ? <ReactPlayer key={`v_v_${data.id}`}
                    className="tb_hs_media_iframe_video"
                    url={data.mediaUrl} controls data-type="video"
                    data-filter-id={data.filterId}
                    data-network={data.network.id}
                    data-link={data.link}
                    data-wall-id={wall.Wall.id} data-item-id={data.id}
                    data-load={0}
                    autoplay={true}
                    muted={true}
                    loop={true}
                    playsinline=""
                /> : (data.network.id == 30) && (data.link.indexOf("vk")) ? <Iframe key={`v_v_${data.id}`}
                    className="tb_hs_media_iframe_video"
                    frameBorder="0"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    src={`${data.mediaUrl}&autoplay=1&muted=1`}
                    allowFullScreen="true"
                    style={{ width: "100%!important" }}
                    data-filter-id={data.filterId}
                    data-type="video"
                    data-network={data.network.id}
                    data-link={data.link}
                    data-wall-id={wall.Wall.id} data-item-id={data.id}
                    data-load={0}
                    autoplay={true}
                    muted={true}
                    loop={true}
                    playsinline=""
                ></Iframe> : data.network.id == 28 ? <div className="tb_hs_media_video">
                    <PopUpTikTok data={data} key={data.id} />
                </div> :
                    <>
                        {
                            (window.navigator.userAgent.includes('Safari') && !window.navigator.userAgent.includes('Chrome')) && data.mediaUrl.includes('instagram') ? <img loading="lazy" key={`v_v_${data.id}`} className="tb_hs_media_video"
                                data-wall-id={wall.Wall.id} data-item-id={data.id}
                                data-type="video"
                                data-network={data.network.id}
                                data-link={data.link}
                                data-load={0}
                                data-filter-id={data.filterId} src={data.mediaUrl} autoplay={true} alt={GET_MEDIA_NAME(data.mediaUrl)}
                                muted={true}
                                loop={true}
                                playsinline="" />
                                :
                                <video
                                    id={`video_p_${data.id}`}
                                    muted={true}
                                    autoplay={true}
                                    loop={true}
                                    playsinline="" className="tb_hs_media_video s" key={`vv_${data.id}`}
                                    data-wall-id={wall.Wall.id} data-item-id={data.id}
                                    data-filter-id={data.filterId}
                                    src={data.mediaUrl}
                                    data-type="video"
                                    data-network={data.network.id}
                                    data-link={data.link}
                                    data-load={0}

                                    onError={(data.stories == 0) ? (e) => { POPUP_VIDEO_RENEW(e) } : null}
                                ></video>
                        }
                    </>
            }
        </div>
    }
}