import React, { PureComponent } from "react";
import { isMobile } from 'react-device-detect';
import { connect } from "react-redux";
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';
import Card from "./Elements/Card";
import { getDataNextSteps } from '../../../actions/themeActions'

class HorizontalColumn extends PureComponent {
  constructor(props) {
    super(props)
    this.inputRefs = {};
  }
  state = {
    windowWidth: document.getElementById('taggbox_main') && document.getElementById('taggbox_main').clientWidth ? document.getElementById('taggbox_main').clientWidth : window.innerWidth,
  }

  componentWillMount() {
    const { renderId } = this.props.renderId
    this.setState({
      windowWidth: renderId ? document.getElementById(renderId).clientWidth : document.getElementById('taggbox_main') && document.getElementById('taggbox_main').clientWidth ? document.getElementById('taggbox_main').clientWidth : window.innerWidth
    })
  }

  requestData = () => {
    const { appendData, wall, preRender, hasMoreData } = this.props;
    if (hasMoreData) {
      getDataNextSteps(
        wall.Wall.id,
        Math.floor(Date.now() / 1000),
        wall.ThemeRule.numberOfPosts,
        appendData.networkID,
        appendData.after,
        preRender,
        appendData.heightEvent
      );
    }
  };
  componentDidMount() {

    const { postData, completeDataObject, onClickPopUpSlider } = this.props;

    if (postData && postData.length == 1) {

      let arrow_left_id = document.querySelector(".tb_hc_arrow_left__")
      let arrow_right_id = document.querySelector(".tb_hc_arrow_right__")
      if (arrow_left_id && arrow_right_id) {
        document.querySelector(".tb_hc_arrow_left__").style = 'display:none'
        document.querySelector(".tb_hc_arrow_right__").style = 'display:none'
      }



      document.body.addEventListener('click', function () { myFunction() });

      myFunction();

      function myFunction() {

        setTimeout(() => {

          let splide01 = document.querySelector(".splide__list");

          if (splide01 != null) {
            splide01.style.transform = 'translateX(0)';
          }
        }, 100);
      }
      window.addEventListener("resize", myFunction);
    }

    let splide02 = document.querySelector("#kt_aside_toggle");
    if (splide02 != null) {
      splide02.addEventListener("click", this.reScrolled)
    }

    // if (this.secondaryRef.current && this.secondaryRef.current.slides.length > 0) {
    //   this.secondaryRef.current.slides.map((item, itemIndex) => {
    //     if (item.classList) {
    //       const filterID = String(item.classList.value).split(" ").filter((fCheck) => String(fCheck).includes("tb_"))
    //       var getID = filterID[0]
    //       if (String(filterID[0]).includes("tb_")) {
    //         getID = String(filterID[0]).replace("tb_", "")
    //       }
    //       const findIndex = postData.findIndex((find) => find == getID)
    //       let clickD = document.querySelector(`#${item.id}`)
    //       clickD.addEventListener('click', event => {
    //         event.preventDefault();
    //         const itemData = completeDataObject[getID]
    //         onClickPopUpSlider(findIndex, itemData)
    //       });
    //     }
    //   })
    // }
  }

  reScrolled = () => {

    setTimeout(() => {
      let splide__list = document.querySelector('.splide__list');
      let getWidth = document.querySelector(".splide__slide").offsetWidth;
      const isVisibleAll = document.querySelectorAll('.splide__slide.is-visible').length;
      const isVisible = document.querySelector('.splide__slide.is-active.is-visible');
      const str = isVisible.getAttribute('aria-label').substring(0, 2)
      const getIndex = Number(str) + (isVisibleAll - 1) + isVisibleAll
      if (getIndex > 0) {
        splide__list.style.transition = '0s ease';
        splide__list.style.transform = `translateX(-${(getIndex * getWidth)}px)`;

      } else {
        splide__list.style.transform = `translateX(0px)`;
      }
    }, 200);
  }


  componentDidUpdate() {

    const value = Object.values(this.inputRefs)[0];
    const { postData, completeDataObject, onClickPopUpSlider, parentID } = this.props;
    if (value && Object.values(value)[5].length > 0) {
      let data = Object.values(value)[5]
      data.map((item, itemIndex) => {
        if (item.classList) {
          const filterID = String(item.classList.value).split(" ").filter((fCheck) => String(fCheck).includes("tb_"))
          var getID = filterID[0]
          if (String(filterID[0]).includes("tb_")) {
            getID = String(filterID[0]).replace("tb_", "")
          }
          const findIndex = postData.findIndex((find) => find == getID)
          let clickD = document.querySelectorAll(`.tb_hc_post_container #${item.id}`)
          for (let index = 0; index < clickD.length; index++) {
            let clickDD = clickD[index];
            let p_id = clickDD.getAttribute('p_id')
            if (String(parentID) === String(p_id)) {
              clickDD.addEventListener('click', event => {
                event.preventDefault();
                const itemData = completeDataObject[getID]
                onClickPopUpSlider(findIndex, itemData)
              });
            }
          }
        }
      })
    }
  }

  render() {
    const { postData, completeDataObject, wall, clickToShowPopUp, hasMoreData, onClickToCTA, parentID } = this.props;
    const inputName = parentID;
    const { windowWidth } = this.state;
    const numberOfCoumn = wall.ThemeRule.numberOfCoumn;
    const mobileColumn = wall.ThemeRule.mobileColumn;
    const slidePost = wall.ThemeRule.slidePost;
    const slideDuration = wall.ThemeRule.slideDuration;
    const autoScrollStatus = wall.Personalization.autoScrollStatus;
    const trimContent = wall.Personalization.trimcontent === 1 ? true : false;
    const isMobileStatus = windowWidth < 768 || isMobile ? true : false
    const manageItemCount = (postData && postData.length) ? isMobileStatus ? 1 : postData.length > 3 ? 3 : postData.length : 3
    const desktopCount = (postData && postData.length) ? isMobileStatus ? 1 : postData.length > 4 ? 4 : postData.length : 4
    const settings = {
      type: autoScrollStatus === 1 || postData.length === 1 ? 'loop' : 'loop',
      rewind: true,
      speed: 1000,
      loop: true,
      interval: slidePost === 1 ? slideDuration * 1000 : 5000,
      perPage: numberOfCoumn > 0 ? numberOfCoumn : isMobileStatus ? 1 : postData.length > 5 ? 5 : postData.length,
      gap: 0,
      autoplay: slidePost === 1 ? true : false,
      padding: "0",
      pagination: false,
      arrows: true,
      fixedHeight: trimContent,
      breakpoints: {
        560: {
          perPage: mobileColumn > 0 ? mobileColumn : 1,
          perMove: mobileColumn > 0 ? mobileColumn : 1
        },
        767: {
          perPage: numberOfCoumn > 0 ? (numberOfCoumn - 3) < 1 ? numberOfCoumn : numberOfCoumn - 3 : manageItemCount,
        },
        991: {
          perPage: numberOfCoumn > 0 ? (numberOfCoumn - 4) < 1 ? numberOfCoumn : (numberOfCoumn - 4) : manageItemCount,
        },
        1200: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn - 1 == 0 ? numberOfCoumn : numberOfCoumn - 1 : manageItemCount,
        },
        1600: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : desktopCount,
        }
      }
    };

    return (<>{parentID != "" ?
      <div className="tb_hc_post_container" ref={this.sliderHeight}>
        <Splide hasTrack={false} className={`tb_hc_post_slider ${trimContent ? 'tb_hc_fixed_height' : ''}`} options={settings} ref={(parentID) => this.inputRefs[inputName] = parentID}>
          <SplideTrack>
            {
              ((postData && postData.length > 0)) && postData.map((item, index) => {
                const cardData = completeDataObject[item];
                return <SplideSlide style={{ margin: 0, padding: 0, }} key={index} p_id={parentID} className={`tb_${cardData.id}`}>
                  <Card ownerId={wall.Wall.owner} itemData={cardData} itemIndex={index} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} onClickToCTA={onClickToCTA} wall={wall} />
                </SplideSlide>
              })}
          </SplideTrack>
          <div className="splide__arrows splide__arrows--ltr tb_hc_arrow_wrapper_" role="navigation" aria-label="Slider Navigation Wrapper">
            <div role="navigation" className="tb_hc_arrow splide__arrow splide__arrow--prev tb_hc_arrow_left__ tb__icon tb-arrow-left-alt" aria-labelledby="Navigation Previous Slide"> </div>
            <div role="navigation" className="tb_hc_arrow splide__arrow splide__arrow--next tb_hc_arrow_right__ tb__icon tb-arrow-right-alt" onClick={hasMoreData != undefined ? this.requestData : null} aria-labelledby="Navigation Next Slide"> </div>
          </div>
        </Splide>
      </div> : null}</>
    );
  }
}




const mapStateToProps = state => {
  return {

  }
}

const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(HorizontalColumn);