import React, { PureComponent } from "react";
import Video from "./Video";
import Image from "./Image";
export default class Media extends PureComponent {
  render() {
    const { data, wallID, wall, ownerId } = this.props;
    const mediaType = (data.type === 3 || data.type === 5) && data.network.id !== 28 && wall.ThemeRule.autoPlay == 1  ? true : false
    return (
      <div className="tb_hs_media_wrap">
        {mediaType ? <Video data={data} wallID={wallID} wall={wall} key={`item_key_video_${data.id}`} /> :
          <Image data={data} wallID={wallID} wall={wall} ownerId={ownerId} />
          }
      </div>
    );
  }
}